import React , {useContext} from 'react'
import { useEffect, useState } from 'react';
import styled from 'styled-components';
import axios from 'axios';
import { useSelector } from 'react-redux';
import { dataContext } from '../App';

interface ReducerType {
  headerReducer: HeaderTypes
}
interface HeaderTypes {
  header: object[]
}

interface Checked {
  content: string,
  date: string
}


const WrapContent = styled.div`
    width: 400px;
    padding: 20px;
    border: 1px solid #eee;
    h1 {
      font-size: 18px;
    }
    input {
      margin: 10px 0;
      height: 30px;
    }
  `

function List() {
  const listDate = useContext(dataContext)

  const header = useSelector(
		(state:ReducerType) => state.headerReducer.header
	);

  const path = process.env.PUBLIC_URL;
	const [comment, setComment] = useState('');
  const [todo, setTodo] = useState<Checked[]>(null)
  
  useEffect(()=>{
    console.log(todo)
  })

  const handleChange = (e:React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    setComment(e.target.value)
  }
  const handleSubmit = () => {
    if(!header) return alert('   !')
    const data = {
			content:comment,
			date:listDate,
		};
    axios.post('/api/write', data).then((res) => {
			if (res) {
				alert('  ');
				console.log(res);
        window.location.replace("/")
			}
		}).catch(function (error) {
      // handle error
      console.dir(error);
    })
    
  }

  const handleKeyDown = (e:React.KeyboardEvent<HTMLInputElement>) => {
    if(e.key === 'Enter') {
      handleSubmit()
    }
  }

  const handleDelete = (el:Checked) => {
    if(!header) return alert('   !')
    const result = window.confirm(' ? ');
		if (!result) return;
		axios.post('/api/delete', { content: el.content }).then((res) => {
			console.log(res);
		}).catch(err=>console.log(err));
		window.location.replace("/")
  }

  useEffect(()=>{
    axios.post('/api/read', {date:listDate}).then((res)=>{
      setTodo(res.data.list)
    })
  },[listDate])
  return (
    <>
      <WrapContent>
        <h1>{listDate && listDate.substring(0,4)+" "+listDate.substring(4,6)+" "+listDate.substring(6,listDate.length)+""}</h1>
        <div>
          <input type="text" value={comment} onChange={handleChange} onKeyPress={handleKeyDown}/>
          <button className="add" onClick={handleSubmit}></button>
        </div>
        <ul className='todolist'>
          {todo && todo.map((el,idx)=>{
            return (
              <li key={idx}>
                <p>{idx+1}. {el.content}</p>
                <button onClick={()=>handleDelete(el)}><img src={`${path}/delete.png`} alt=""/></button>
              </li>
            )
          })}
        </ul>
      </WrapContent>
    </>
  )
}

export default List