import React, {memo, useState} from 'react'
import { signInWithEmailAndPassword } from "firebase/auth";
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {auth1} from './firebase';

function Header() {
  const dispatch = useDispatch();
  const auth = auth1;
  const navi = useNavigate();
  const [email, setEmail] = useState('');
	const [pw1, setPw1] = useState('');

  const handleJoin = async () => {
		if (!email || !pw1) {
			return alert('  ');
		}

		
    await signInWithEmailAndPassword(auth, email, pw1)
    .then((userCredential) => {
      alert(' ')
      // Signed in 
      const user = userCredential.user;
      dispatch({ type: 'SET_HEADER', payload: user });
      navi('/')
      // ...
    })
    .catch((error) => {
      alert('  ')
    });

	};



  return (
    <header>
      <p>    </p>
      <div>
        <p></p>
        <input type="text" value={email} onChange={(e)=>setEmail(e.target.value)} placeholder=" "/>
        <input type="password" value={pw1} onChange={(e)=>setPw1(e.target.value)} placeholder=" "/>
        <button onClick={handleJoin}></button>
      </div>
    </header>
  )
}

export default memo(Header)